<template>
  <div class="page">
    <div class="content">
      <ul class="articles">
        <li>
          <div class="time">{{info.time}}</div>
          <div class="title">{{info.title}}</div>
          <div class="ms" v-html="info.nr"></div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
    name: 'articles',
    data() {
        return {
            info: []
        }
    },
    watch: {
        $route: {
            handler(route) {
                let id = Number(route.params.id)
                fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/life').then((rs) => {
                    return rs.json()
                }).then((data) => {
                    let fi=data.filter((el)=>{
                        return el.id===id
                    })
                    this.info = fi[0]
                })

            },
            immediate: true
        }
    },
    methods: {},
    computed: {}
}
</script>

